.wishes-section {
	position: relative;
	padding: 5rem 0;
	background: linear-gradient(180deg, var(--color-primary) 0%, transparent 100%);
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 10%;
		left: 0;
		width: 320px;
		transform: translateX(-30%);
		@include max-md {
			width: 250px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 30%;
		right: 0;
		width: 200px;
		transform: translateX(45%);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 120px;
		}
		img {
			transform: 80% 100%;
		}
	}
	.elements-absolute {
		transform: translateY(30%);
	}
	.card-form {
		margin-bottom: 0;
		background-color: transparent;
		border-radius: var(--border-radius);
		position: relative;
		z-index: 3;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			content: "";
			background: var(--color-main);
			opacity: 0.8;
			border-radius: var(--border-radius);
		}
		&.card {
			border: none;
		}
		.card-body {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		@include max-lg {
			margin-top: 2rem;
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			border-radius: var(--border-radius);
			.card-body {
				overflow: hidden;
				padding: 2rem 0;
				.wishes {
					text-align: left;
					height: 28rem;
					overflow: auto;
					padding-right: 1rem;
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							position: relative;
							&::before {
								position: absolute;
								top: 0;
								right: 0;
								bottom: 0;
								left: 0;
								content: "";
								border-radius: 5px;
								background-color: var(--color-main);
								opacity: 0.8;
							}
							&::after {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: var(--color-main);
								opacity: 0.8;
							}
							* {
								position: relative;
							}
						}
					}
				}
			}
		}
	}
}
